@import "../../variable.scss";
.outlet-container {
    min-width: 600px;
    max-width: 600px;
    display: flex;
    border: 1px solid #dbdbdb;
    max-height: 600px;
    min-height: 600px;
    overflow: hidden;
    box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
}

.city-selection-container {
    width: 50%;
    background-color: #f7f7f7;
    overflow: auto;
}

.outlet-selection-container {
    width: 50%;
    background-color: white;
    overflow: auto;
}

.ui.input {
    display: flex !important;
}

.outlet-filter-list-item {
    padding: $spacing-m;
    outline: none;
}

.outlet-sticky-section {
    position: sticky;
    top: 0;
    background-color: inherit;
    z-index: 2;
}

.non-selected-item {
    border-left: 6px solid transparent;
}

.selected-item {
    position: relative;
    border-left: 6px solid var(--primary-blue);
    color: var(--primary-blue);
}

.o-t-search-container {
    padding: 16px 24px;
}

.hover-c-i {
    transition: all 0.2s ease-in-out;
}

.hover-c-i:hover {
    background-color: rgba(0, 0, 0, 0.03);
}

.o-s-upper-label-container {
    display: flex;
    align-items: center;
    padding: 24px;
}

.o-s-label-count {
    margin-left: 8px;
    font-size: 12px;
}

@media only screen and (min-width: 320px) and (max-width: 768px) {
    .o-s-upper-label-container {
        flex-direction: column;
        padding: 16px;
    }
    .o-s-label-count {
        text-align: center;
        font-size: 8px!important;
        margin-left: 0px;
    }
}

@media only screen and (min-width: 320px) and (max-width: 992px) {
    .outlet-container {
        min-width: 320px;
        height: 100%;
    }
    .outlet-filter-list-item {
        padding: 16px;
        font-size: 12px;
    }
    .o-t-search-container {
        padding: 8px 16px;
    }
}


